<script>
export default
  {
    name: "navbar",
    mounted() {
      window.addEventListener('scroll', this.scrollShowNavbar, true);//監聽滾動條
    },
    methods: {

      //為了計算距離頂部的高度，當高度大於60背景顯示黑色
      scrollShowNavbar() {
        const that = this;
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        that.scrollTop = scrollTop;
        if (that.scrollTop > 60) {
          const element = document.querySelector("nav");
          element.style.backgroundColor = 'black';
        } else {
          const element = document.querySelector("nav");
          element.style.backgroundColor = '#ffffff00';
        }
      }
    },
    destroyed() {
      window.removeEventListener('scroll', this.scrollShowNavbar)
    }
  };
</script>

<template>
  <div id="navbar">
    <nav class="navbar navbar-expand-lg" data-bs-theme="bg-transparent">
      <div class="container-fluid">
        <a class="navbar-brand" href="#">
          <img src="@/assets/images/ACW-LOGO.png">
        </a>
        <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNavDropdown"
          aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse justify-content-end" id="navbarNavDropdown">
          <ul class="nav navbar-nav">
            <li class="nav-item">
              <a class="nav-link active" aria-current="page" href="#">Home</a>
            </li>
            <li class="nav-item dropdown">
              <a class="nav-link dropdown-toggle" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Solution
              </a>
              <ul class="dropdown-menu">
                <li><a class="dropdown-item" href="#">Action</a></li>
                <li><a class="dropdown-item" href="#">Another action</a></li>
                <li><a class="dropdown-item" href="#">Something else here</a></li>
              </ul>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">About us</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">Partnership</a>
            </li>
          </ul>
        </div>

        <button type="button" class="btn" href="#">Contacts Us</button>
      </div>
    </nav>
  </div>
</template>

<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');

nav {
  width: 100vw;
  z-index: 98;
  position: fixed;
  font-family: 'Rubik', sans-serif;

  .container-fluid {
    margin-left: 10%;
    margin-right: 10%;
  }

  .nav.navbar-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    gap: 2.5rem;

    >li>a {
      color: #FFFFFF;
    }
  }

  .btn {
    border: 1px solid #FFFFFF;
    border-radius: 75px;
    color: #FFFFFF;
    margin-left: 3%;
  }
}

