<template>
    <div id="connectUsImg" class="img_box" v-show="showIcon" @click="backTop()" src="@/assets/images/up.png">
        <img src="@/assets/images/up.png" />
    </div>
</template>

<script >
export default{
    name: "connectUsImg",
    data(){
        return{
            showIcon:false //隱藏置頂圖標
        };
    },
    mounted(){
        window.addEventListener('scroll', this.scrollBackTop, true);//監聽滾動條
    },
    methods:{
        //點擊圖片回到頂部方法, 加計時器為了過程滑順
        backTop(){
            let top = document.documentElement.scrollTop || document.body.scrollTop;
            let timer = setInterval(() =>{
            document.body.scrollTop = document.documentElement.scrollTop = top -= 50;
            if(top <= 0){
                clearInterval(timer)
            }
            },10);
        },

        //為了計算距離頂部的高度，當高度大於60顯示回頂部圖示，小60則隱藏
        scrollBackTop(){
            const that = this;
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            that.scrollTop = scrollTop;
            if(that.scrollTop > 60){
                that.showIcon = true;
            } else {
                that.showIcon = false;
            }
        }
    },
    destroyed(){
        window.removeEventListener('scroll', this.scrollBackTop)
    }
};
</script>

<style lang="scss" scoped>
.img_box{
    width:3.5rem;
	height:3.5rem;
    background:#FFFFFF;
	box-shadow:0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2);
    background-size: 100% 100%;
    position: fixed;
    display:flex;
    align-items: center;
    justify-content: center;
    z-index: 98;
    right:2rem;
	bottom:2rem;
    border-radius: 50%;
    img{
        
    width:2.3rem;
	height:2.3rem;
    }
}

</style>