   
<script>
export default {
    data() {
        return {
            backgroundColor: "#3A4661",
            boxShadow: "0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2);",
        };
    },
    methods: {
        // 1、进入元素
        mouseover() {
            console.log('mouseover')
            this.backgroundColor = '#FFFFFF'
            this.boxShadow = '0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2)'
        },
        // 5、离开元素
        mouseout() {
            this.backgroundColor = '#3A4661'
            this.boxShadow = '0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2)'
            console.log('mouseout')
        },
        // 6、鼠标在元素上 按下
        mousedown() {
            this.backgroundColor = '#bacfff'
            this.boxShadow = '0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2)'
            console.log('mousedown')
        },
        // 7、鼠标在元素上 抬起
        mouseup() {
            this.backgroundColor = '#FFFFFF'
            this.boxShadow = '0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2)'
            console.log('mouseup')
        }
    },
    props: {
        btn_text: String,
    },
    setup(props) {
        return { props };
    },
}
</script>
<template>
    <div class="btn-view" @mouseover="mouseover" @mouseout="mouseout" @mousedown="mousedown" @mouseup="mouseup"
        :style="{ backgroundColor: backgroundColor, boxShadow: boxShadow }">
        {{ props.btn_text }}
    </div>
</template>

<style lang="scss" scoped>
.btn-view {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0.375rem 1.0625rem;
    gap: 0.625rem;
    width: 20rem;
    height: 20rem;
    background: #3A4661;
    /* box-shadow:0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2); */
    border-radius: 1.75rem;
    transition-duration: 0.3s;
    color: #FFFFFF;

}

.btn-view:hover {
    border: 1px solid #3A4661;
    /* box-shadow:0rem 0.25rem 0.3125rem rgba(0,0,0,0.14),0rem 0.0625rem 0.625rem rgba(0,0,0,0.12),0rem 0.125rem 0.25rem rgba(0,0,0,0.2); */
    border-radius: 1.75rem;
    color: black;
}
